$primary-color: #836BD1; 
$secondary-color: #FF295C;
$tertiary-color: #FF9C29;

// COLORS BY NAME

$black: #000000;
$white: #ffffff;
$purple: #836BD1; 
$light-purple: #E2DCF9;
$pink: #FF295C;
$light-pink: #FFEBF3;
$yellow: #FF9C29;
$light-yellow: #F8F4E8;

$salsa: #4BCD4A;
$bachata: #FDA219; 
$ballet: #FE94B9; 
$contemporaneo: #997FFF; 
$high-heels: #DF1552; 
$kizomba: #DD664D; 
$latinas: #A72901;
$latinas-kids: #FF630C; 
$pop-dance: #55ADFF; 


// MIXINS

//Grid

@mixin grid-general-purpose($grid-template-columns, $grid-template-rows, $gap, $padding) {
    display:grid;
    grid-template-columns: $grid-template-columns;
    grid-template-rows: $grid-template-rows;
    gap: $gap;
    padding: $padding;
}

//Button @include button-general-purpose($purple, $purple, $purple, $purple);

@mixin button-general-purpose ($button-background-color, $button-border, $hover-border-color, $hover-color){

    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;
    text-decoration: none;
    text-transform: uppercase;
    filter: drop-shadow(0px 4px 4px rgba(121, 121, 121, 0.4));
    background: $button-background-color;
    border: 2px solid $button-border;
    border-radius: 5px;
    font-weight: 500;
    font-size: 0.9em;
    font-family: 'Basier Square';
    letter-spacing: 0.02em;
    color: white;
    padding: 12px 30px;
    cursor: pointer;

    &:hover {
        background: transparent;
        border: 2px solid $hover-border-color;
        color:$button-background-color;
        filter: drop-shadow(0px 4px 4px rgba(121, 121, 121, 0));
        transition: opacity .2s ease-out;
        -moz-transition: opacity .2s ease-out;
        -webkit-transition: opacity .2s ease-out;
        -o-transition: opacity .2s ease-out; 
    }

}

//Price box

@mixin price-box ($box-border) {
    border: 2px solid $box-border;
    border-radius: 5px;
    cursor: pointer;
}

@mixin add-price-list ($fill-color) {
    position: absolute;
    right: 12px;
    bottom: 12px;
    transform: rotate(45deg);

    path {   
        fill:$fill-color;
    }
}

